/** подготовка графики - ретина **/
html {
  box-sizing: border-box;
  height: 100%; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  position: relative;
  display: flex;
  flex-direction: column;
  font-family: "OpenSans", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  cursor: default; }

a,
button {
  cursor: pointer; }

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0; }

@keyframes scale {
  from {
    transform: scale(1); }
  to {
    transform: scale(1.1); } }

@keyframes hover-color-from-red {
  from {
    background-color: #e74c3c; }
  to {
    background-color: #fcd12a; } }

@keyframes hover-color-from-gray {
  from {
    background-color: #999999; }
  to {
    background-color: #fcd12a; } }

@keyframes hover-color-from-translate {
  from {
    background-color: translate; }
  to {
    background-color: #fcd12a; } }

@keyframes hover-menu {
  from {
    color: #ffffff;
    border-bottom: 1px solid transparent;
    padding-bottom: 10px; }
  to {
    color: #e74c3c;
    border-bottom: 1px solid #e74c3c;
    padding-bottom: 20px; } }

@keyframes hover-about {
  from {
    border: 1px solid #cbc6c0;
    transform: rotate(0); }
  to {
    border: 1px solid #e74c3c;
    transform: rotate(45deg); } }

@keyframes hover-color-about {
  from {
    color: #6a6a6a; }
  to {
    color: #e74c3c; } }

@keyframes hover-filter {
  from {
    color: #999999; }
  to {
    color: #e74c3c; } }

@keyframes hover-mask {
  from {
    opacity: 0; }
  to {
    opacity: 0.8; } }

@keyframes hover-paginator {
  from {
    background-color: #f4f5f9; }
  to {
    background-color: #fcd12a; } }

@keyframes hover-paginator-active {
  from {
    background-color: #e74c3c; }
  to {
    background-color: #fcd12a; } }

@keyframes hover-pricing {
  from {
    background-color: #999999; }
  to {
    background-color: #e74c3c; } }

@keyframes hover-clients {
  from {
    box-shadow: 0 1px 3px 0 rgba(31, 31, 31, 0.09);
    filter: grayscale(100%);
    opacity: 0.3; }
  to {
    box-shadow: 0 3px 9px 1px rgba(31, 31, 31, 0.3);
    filter: grayscale(0);
    opacity: 1; } }

@keyframes hover-article {
  from {
    color: #ffffff; }
  to {
    color: #fcd12a; } }

@keyframes hover-blog {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

